.list_section {
  max-width: 1400px;
  margin: 0 auto;
  padding-top: 50px;
  padding-bottom: 50px;
}

.list {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: -30px;
  margin-left: -30px;
  flex-wrap: wrap;
}

.item {
  flex-basis: calc(100% / 4 - 30px);
  margin-top: 30px;
  margin-left: 30px;
  border-radius: 5px;
  overflow: hidden;
  align-items: center;
  justify-content: center;
  background-color: bisque;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  transform: scale(1);
  transition: all 250ms linear;
}

.item:hover,
.item:focus {
  transform: scale(1.1);
  transition: all 250ms linear;
}

.item_image {
  width: 100%;
  height: 450px;
  overflow: hidden;
}

.item_name {
  padding-top: 10px;
  padding-bottom: 10px;
}
